@import url(https://fonts.googleapis.com/css?family=Proza+Libre);
@import url(https://js.arcgis.com/4.9/esri/css/main.css);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input[type="file"].uploadImageInput {
  display: none; }

.imageComponentButtonText {
  margin-right: 10px; }

.selectedImagePreview {
  margin-top: 10px; }

.uploadSidewalkImageText {
  padding-left: 10px; }

.infiniteImageListRow {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0 10px;
  background-color: #fff; }

.infiniteImageRowSelected {
  border-radius: 5%;
  box-shadow: 0 0 0 2px #9C27B0;
  cursor: pointer;
  width: 90%;
  height: 90%; }

.infiniteImageRowUnselected {
  cursor: pointer; }

.infiniteImageRowUnselected:hover {
  border-radius: 5%;
  box-shadow: 0 0 0 2px #FFEB3B;
  width: 90%;
  height: 90%; }

.backgroundImage {
  max-width: 70vw;
  max-height: 100vh; }

.labelledImageContainer {
  position: relative;
  text-align: center;
  color: white; }

div.noOutlineDiv:focus {
  outline: none; }

.selectedImageWrapper {
  display: flex;
  margin: auto;
  align-items: center;
  justify-content: center; }

.closeImageListButton {
  float: right;
  cursor: pointer;
  margin-left: auto;
  margin-right: 0; }

#mapContainer {
  min-height: 100vh;
  cursor: pointer; }

/* bootstrap button overrides */
.btn-primary {
  background-color: #9C27B0 !important; }

.btn-primary:hover, .btn-primary:focus {
  background-color: #6A1B9A !important; }

/* default font */
body {
  font-family: "Proza Libre", sans-serif; }

.row {
  display: flex;
  justify-content: space-between;
  width: 75%;
  align-items: center; }
  .row :nth-child(1) {
    display: block;
    margin-right: 5px;
    margin-left: 14px; }

.padding25 {
  padding: 25px 25px 25px 25px; }

